import { graphql } from 'gatsby';
import PropTypes from 'prop-types';
import React from 'react';
import About from '../components/about';
import CardGrid from '../components/card-grid';
import Contact from '../components/contact';
import FeaturedProjects from '../components/featured-projects';
import Hero from '../components/hero';
import Facts from '../components/facts';
import Layout from '../components/layout';
// import RecentPosts from '../components/recent-posts';
import SEO from '../components/seo';
import { indexMenuLinks } from '../components/_config/menu-links';
import { mobileMenuLinks } from '../components/_config/mobile-menu-links';

const Index = ({ data }) => {
  const heroData = {
    author: data.site.siteMetadata.author,
    tagline: data.hero.frontmatter.tagline,
    description: data.hero.html,
    introduction: data.hero.frontmatter.introduction,
    ctaEmailLabel: data.hero.frontmatter.cta_email_label,
    ctaEmailLink: data.hero.frontmatter.cta_email_link,
    ctaEmailIcon: data.hero.frontmatter.cta_email_icon,
    ctaZaloLabel: data.hero.frontmatter.cta_zalo_label,
    ctaZaloLink: data.hero.frontmatter.cta_zalo_link,
    ctaZaloIcon: data.hero.frontmatter.cta_zalo_icon,
    ctaPhoneLabel: data.hero.frontmatter.cta_phone_label,
    ctaPhoneLink: data.hero.frontmatter.cta_phone_link,
    ctaPhoneIcon: data.hero.frontmatter.cta_phone_icon,
    ctaSkypeLabel: data.hero.frontmatter.cta_skype_label,
    ctaSkypeLink: data.hero.frontmatter.cta_skype_link,
    ctaSkypeIcon: data.hero.frontmatter.cta_skype_icon,
  };

  return (
    <Layout menuLinks={indexMenuLinks} mobileMenuLinks={mobileMenuLinks}>
      <SEO title="Cung cp dch v thit k website v pht trin ng dng mobiel, desktop chuyn nghip." />
      <Hero data={heroData} />
      <Facts />
      <About data={data.about} />
      <CardGrid cards={data.cards.frontmatter.cards} description={data.cards.html} title="Dch v" id="services" />
      <FeaturedProjects featured={data.featuredProjects.nodes} />
      {/* <RecentPosts data={data.blog.edges} /> */}
      <Contact data={data.contact} />
    </Layout>
  );
};

Index.propTypes = {
  data: PropTypes.object.isRequired,
};

export default Index;

export const query = graphql`
  {
    site {
      siteMetadata {
        author
      }
    }

    hero: markdownRemark(fileAbsolutePath: { regex: "/content/sections/hero/" }) {
      frontmatter {
        introduction
        tagline
        cta_email_label
        cta_email_link
        cta_email_icon
        cta_skype_label
        cta_skype_link
        cta_skype_icon
        cta_phone_label
        cta_phone_link
        cta_phone_icon
        cta_zalo_label
        cta_zalo_link
        cta_zalo_icon
      }
      html
    }

    about: markdownRemark(fileAbsolutePath: { regex: "/content/sections/about/" }) {
      frontmatter {
        title
        techs
        about_image {
          childImageSharp {
            fluid(maxWidth: 800) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
      html
    }

    cards: markdownRemark(fileAbsolutePath: { regex: "/content/sections/cards/" }) {
      frontmatter {
        cards {
          label
          icon
          prefix
          description
        }
      }
      html
    }

    featuredProjects: allMarkdownRemark(
      limit: 10
      sort: { order: DESC, fields: frontmatter___date }
      filter: { fileAbsolutePath: { regex: "/content/projects/" }, frontmatter: { featured: { eq: true } } }
    ) {
      nodes {
        frontmatter {
          date(formatString: "D MMMM, YYYY")
          title
          repo_link
          demo_link
          techs
          cover_image {
            childImageSharp {
              fluid(maxWidth: 800) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        html
      }
    }

    contact: markdownRemark(fileAbsolutePath: { regex: "/content/sections/contact/" }) {
      frontmatter {
        phone
        email
        address
      }
      html
    }
  }
`;

